<template>
  <div>
    <h4>  </h4>
    <el-row>
      <el-col style="width:300px;">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item label=":">
            <el-select v-model="unionId" clearable placeholder="">
              <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col style="width:100px;">
        <div class="grid-content bg-purple">
          <el-select v-model="value" clearable placeholder="" class="fl">
            <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </div>
      </el-col>
      <el-col style="width:200px;">
        <div class="grid-content bg-purple">
          <el-input placeholder="" icon="search" v-model="input" :on-icon-click="search" class="input-search2 fl">
          </el-input>
        </div>
      </el-col>
    </el-row>
    <!--  -->
    <div class="businesTable">
      <el-table :data="tableData" style="width: 100%">
        <el-table-column prop="clientName" label="">
        </el-table-column>
        <el-table-column prop="clientPhone" label="">
        </el-table-column>
        <el-table-column prop="businessMsg" label="">
        </el-table-column>
        <el-table-column prop="fromEnterpriseName" label="">
        </el-table-column>
        <el-table-column prop="unionName" label="">
        </el-table-column>
        <el-table-column prop="isAccept" label="" :filters="[{ text: '', value: '' }, { text: '', value: '' }, { text: '', value: '' }]" :filter-method="filterTag" filter-placement="bottom-end" width="150">
          <template scope="scope">
            <el-tag :type="scope.row.isAccept === '' ? 'warning' : (scope.row.isAccept === '' ? 'success' : 'danger')">{{scope.row.isAccept}}</el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="" label="" min-width="120" ref="test">
          <template scope="scope">
            <div class="sizeAndColor">
              <el-button @click="showDialog(scope)" size="small"></el-button>
              <el-button v-if="scope.row.isAccept === ''" @click="agree(scope)" size="small"></el-button>
              <el-button v-if="scope.row.isAccept === ''" @click="disagree(scope)" size="small"></el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <!--   -->
    <div class="model_01">
      <el-dialog title="" :visible.sync="dialogVisible" size="tiny" @click="dialogVisible = false">
        <hr>
        <div class="model_detail">
          <p>{{ detailData.clientName }}</p>
          <p>{{ detailData.clientPhone }}</p>
          <p>{{ detailData.fromEnterpriseName }}</p>
          <p>{{ detailData.unionName }}</p>
          <p>{{ detailData.isAccept }}</p>
          <p>{{ detailData.businessMsg }}</p>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="dialogVisible = false"> </el-button>
        </span>
      </el-dialog>
    </div>
    <!--   -->
    <div class="model_01">
      <el-dialog title="" :visible.sync="dialogVisible1" size="tiny" @close="resetData">
        <hr>
        <div class="model_detail">
          <span></span>
          <el-row style="width:250px;margin-top:10px;">
            <el-input v-model="acceptancePrice" placeholder=""></el-input>
          </el-row>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="confirm"></el-button>
          <el-button @click="dialogVisible1=false"></el-button>
        </span>
      </el-dialog>
    </div>
    <!--   -->
    <div class="model_02">
      <el-dialog title="" :visible.sync="dialogVisible2" size="tiny">
        <span slot="footer" class="dialog-footer">
          <el-button type="primary" @click="confirm1"></el-button>
          <el-button @click="dialogVisible2 = false"></el-button>
        </span>
      </el-dialog>
    </div>
    <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalAll">
    </el-pagination>
  </div>
</template>

<script>
import $http from '@/utils/http.js'
export default {
  name: 'mybusiness',
  data() {
    return {
      unionId: '',
      options1: [],
      value: '',
      options2: [
        {
          value: 'clientName',
          label: ''
        },
        {
          value: 'clientPhone',
          label: ''
        },
      ],
      input: '',
      tableData: [],
      currentPage: 1,
      dialogVisible: false,
      dialogVisible1: false,
      dialogVisible2: false,
      detailData: {
        userName: '',
        userPhone: '',
        enterpriseName: '',
        unionName: '',
        isAccept: '',
        businessMsg: '',
      },
      acceptancePrice: '',
      opportunityId: '',
      //;
      totalAll: 0,
    }
  },
  mounted: function() {
    // 
    $http.get(`/unionMain/list/myUnion`)
      .then(res => {
        if (res.data.data) {
          this.options1 = res.data.data;
          this.options1.forEach((v, i) => {
            v.value = v.id;
            v.label = v.name;
          });
        } else {
          this.options1 = [];
        };
      })
      .catch(err => {
        this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
      });
    // 
    $http.get(`/unionOpportunity/toMe`)
      .then(res => {
        if (res.data.data) {
          this.tableData = res.data.data.records;
          this.totalAll = res.data.data.total;
          this.tableData.forEach((v, i) => {
            switch (v.isAccept) {
              case 1:
                v.isAccept = '';
                break;
              case 2:
                v.isAccept = '';
                break;
              case 3:
                v.isAccept = ''
            };
          });
        } else {
          this.tableData = [];
          this.totalAll = 0;
        };
      })
      .catch(err => {
        this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
      })
  },
  watch: {
    unionId: function() {
      $http.get(`/unionOpportunity/toMe?current=1&unionId=${this.unionId}&` + this.value + '=' + this.input)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records;
            this.totalAll = res.data.data.total;
            this.tableData.forEach((v, i) => {
              switch (v.isAccept) {
                case 1:
                  v.isAccept = '';
                  break;
                case 2:
                  v.isAccept = '';
                  break;
                case 3:
                  v.isAccept = ''
              };
            });
          } else {
            this.tableData = [];
            this.totalAll = 0;
          };
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    }
  },
  methods: {
    // 
    search() {
      $http.get(`/unionOpportunity/toMe?current=1&unionId=${this.unionId}&` + this.value + '=' + this.input)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records;
            this.totalAll = res.data.data.total;
            this.tableData.forEach((v, i) => {
              switch (v.isAccept) {
                case 1:
                  v.isAccept = '';
                  break;
                case 2:
                  v.isAccept = '';
                  break;
                case 3:
                  v.isAccept = ''
              };
            });
          } else {
            this.tableData = [];
            this.totalAll = 0;
          };
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    handleCurrentChange(val) {
      $http.get(`/unionOpportunity/toMe?current=${val}&unionId=${this.unionId}&` + this.value + '=' + this.input)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records;
            this.totalAll = res.data.data.total;
            this.tableData.forEach((v, i) => {
              switch (v.isAccept) {
                case 1:
                  v.isAccept = '';
                  break;
                case 2:
                  v.isAccept = '';
                  break;
                case 3:
                  v.isAccept = ''
              };
            });
          } else {
            this.tableData = [];
            this.totalAll = 0;
          };
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    //  
    showDialog(scope) {
      this.dialogVisible = true;
      this.detailData = scope.row;
    },
    // 
    agree(scope) {
      this.dialogVisible1 = true;
      this.opportunityId = scope.row.opportunityId; // todo 
    },
    // 
    confirm() {
      $http.put(`/unionOpportunity/${this.opportunityId}/isAccept/2`, this.acceptancePrice)
        .then(res => {
          this.dialogVisible1 = false;
          $http.get(`/unionOpportunity/toMe?current=${this.currentPage}&unionId=${this.unionId}&` + this.value + '=' + this.input)
            .then(res => {
              if (res.data.data) {
                this.tableData = res.data.data.records;
                this.totalAll = res.data.data.total;
                this.tableData.forEach((v, i) => {
                  switch (v.isAccept) {
                    case 1:
                      v.isAccept = '';
                      break;
                    case 2:
                      v.isAccept = '';
                      break;
                    case 3:
                      v.isAccept = ''
                  };
                });
              } else {
                this.tableData = [];
                this.totalAll = 0;
              };
            })
            .catch(err => {
              this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
            })
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    disagree(scope) {
      this.dialogVisible2 = true;
      this.opportunityId = scope.row.opportunityId; // todo 
    },
    // 
    confirm1() {
      $http.put(`/unionOpportunity/${this.opportunityId}/isAccept/3`)
        .then(res => {
          this.dialogVisible2 = false;
          $http.get(`/unionOpportunity/toMe?current=${this.currentPage}&unionId=${this.unionId}&` + this.value + '=' + this.input)
            .then(res => {
              if (res.data.data) {
                this.tableData = res.data.data.records;
                this.totalAll = res.data.data.total;
                this.tableData.forEach((v, i) => {
                  switch (v.isAccept) {
                    case 1:
                      v.isAccept = '';
                      break;
                    case 2:
                      v.isAccept = '';
                      break;
                    case 3:
                      v.isAccept = ''
                  };
                });
              } else {
                this.tableData = [];
                this.totalAll = 0;
              };
            })
            .catch(err => {
              this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
            })
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    filterTag(value, row) {
      return row.isAccept === value;
    },
    // 
    resetData() {
      this.acceptancePrice = '';
    },
  }
}
</script>

<style lang='less' rel="stylesheet/less">
.model_01 {
  .el-dialog__body {
    padding: 0;
  }
  .el-dialog--tiny {
    width: 660px!important;
  }
  .model_detail {
    margin: 19px 0 10px 30px;
    p {
      margin-bottom: 15px;
    }
  }
}

.model_02 {
  .el-dialog--tiny {
    width: 660px!important;
  }
  .el-dialog__footer {
    padding: 70px 20px 15px!important;
  }
}
</style>



