<template>
  <!--  -->
  <div>
    <!---->
    <el-row>
      <el-col style="width:300px;">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item label=":">
            <el-select v-model="unionId" clearable placeholder="">
              <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col style="width:300px;">
        <el-form :inline="true" class="demo-form-inline">
          <el-form-item label=":">
            <el-select v-model="fromMemberId" clearable placeholder="">
              <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </el-form-item>
        </el-form>
      </el-col>
      <el-col style="width:100px;">
        <div class="grid-content bg-purple">
          <el-select v-model="value" clearable placeholder="" class="fl">
            <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </div>
      </el-col>
      <el-col style="width:200px;">
        <div class="grid-content bg-purple">
          <el-input placeholder="" icon="search" v-model="input" :on-icon-click="search" class="input-search2 fl">
          </el-input>
        </div>
      </el-col>
    </el-row>
    <!--  -->
    <el-table :data="tableData" style="width: 100%" ref="multipleTable" @selection-change="handleSelectionChange">
      <el-table-column type="selection" min-width="55px"></el-table-column>
      <el-table-column prop="clientName" label="" min-width="100px">
      </el-table-column>
      <el-table-column prop="clientPhone" label="" min-width="100px">
        <template scope="scope">
          <el-popover trigger="hover" placement="bottom">
            <p>: {{ scope.row.clientPhone }}</p>
            <div slot="reference" class="name-wrapper">
              <span>{{ scope.row.clientPhone }}</span>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="businessMsg" label="" min-width="100px">
        <template scope="scope">
          <el-popover trigger="hover" placement="bottom">
            <p>{{ scope.row.businessMsg }}</p>
            <div slot="reference" class="name-wrapper">
              <span>{{ scope.row.businessMsg }}</span>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column prop="fromEnterpriseName" label="()" min-width="135px">
      </el-table-column>
      <el-table-column prop="unionName" label="" min-width="100px">
      </el-table-column>
      <el-table-column prop="acceptPrice" label="()" min-width="125">
      </el-table-column>
      <el-table-column prop="brokeragePrice" label="()" min-width="120">
      </el-table-column>
      <el-table-column prop="opportunityType" label="" min-width="100px">
      </el-table-column>
      <el-table-column prop="isClose" label="" width="140" :filters="[{ text: '', value: '' }, { text: '', value: '' }]" :filter-method="filterTag" filter-placement="bottom-end">
        <template scope="scope">
          <el-tag :type="scope.row.isClose === '' ? 'danger' : 'success'">{{scope.row.isClose}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="lastModifyTime" label="" min-width="120">
        <template scope="scope">
          <el-popover trigger="hover" placement="bottom">
            <p>{{ scope.row.lastModifyTime }}</p>
            <div slot="reference" class="name-wrapper">
              <span>{{ scope.row.lastModifyTime }}</span>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <div class="sizeAndColor">
            <el-button v-if="scope.row.isClose === ''" @click="pay(scope)"></el-button>
          </div>
        </template>
      </el-table-column>
    </el-table>
    <div style="position: relative;padding: 5px 0;">
      <el-button @click="payAll" v-bind:disabled="!canPay" style="position: absolute;top:15px;left:0"></el-button>
      <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalAll">
      </el-pagination>
    </div>
    <!--   -->
    <div class="model_0">
      <el-dialog title="" :visible.sync="dialogVisible" size="tiny" @close="resetData">
        <hr>
        <div class="middle_">
          <img v-bind:src="imgSrc" class="imgSrc">
          <p>
            <span>{{ brokeragePrice }}</span>
          </p>
          <p></p>
        </div>
        <span slot="footer" class="dialog-footer">
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import $http from '@/utils/http.js'
import $todate from '@/utils/todate.js'
export default {
  name: 'commission-income',
  data() {
    return {
      unionId: '',
      memberId: '',
      options1: [],
      fromMemberId: '',
      options2: [],
      value: '',
      options3: [
        {
          value: 'clientName',
          label: ''
        },
        {
          value: 'clientPhone',
          label: ''
        },
      ],
      input: '',
      tableData: [],
      currentPage: 1,
      brokeragePrice: 0,
      dialogVisible: false,
      opportunityId: '',
      imgSrc: '',
      canPay: false,
      totalAll: 0,
      only: '',
    }
  },
  watch: {
    unionId: function() {
      this.fromMemberId = '';
      this.options2 = [];
      // unionIdmemberId
      $http.get(`/unionMember/listMap`)
        .then(res => {
          if (res.data.data) {
            res.data.data.forEach((v, i) => {
              if (v.unionMain.id === this.unionId) {
                this.memberId = v.unionMember.id;
                $http.get(`/unionMember/listMap/memberId/${this.memberId}`)
                  .then(res => {
                    if (res.data.data) {
                      this.options2 = res.data.data;
                      res.data.data.forEach((v, i) => {
                        this.options2[i].value = v.memberId;
                        this.options2[i].label = v.enterpriseName;
                      });
                    } else {
                      this.options2 = [];
                    };
                  })
                  .catch(err => {
                    this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
                  })
              };
              this.search();
            });
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    fromMemberId: function() {
      this.search();
    }
  },
  mounted: function() {
    // 
    $http.get(`/unionMember/listMap`)
      .then(res => {
        if (res.data.data) {
          this.options1 = res.data.data;
          res.data.data.forEach((v, i) => {
            this.options1[i].value = v.unionMain.id;
            this.options1[i].label = v.unionMain.name;
          });
        } else {
          this.options1 = [];
        };
      })
      .catch(err => {
        this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
      });
    $http.get(`/unionOpportunity/expense?current=1`)
      .then(res => {
        if (res.data.data) {
          this.tableData = res.data.data.records;
          this.totalAll = res.data.data.total;
          this.tableData.forEach((v, i) => {
            v.lastModifyTime = $todate.todate(new Date(v.lastModifyTime));
            switch (v.isClose) {
              case 0:
                v.isClose = '';
                break;
              case 1:
                v.isClose = '';
                break;
            };
            switch (v.opportunityType) {
              case 1:
                v.opportunityType = '';
                break;
              case 2:
                v.opportunityType = '';
                break;
            };
          });
        } else {
          this.tableData = [];
          this.totalAll = 0;
        };
      })
      .catch(err => {
        this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
      })
  },

  methods: {
    // 
    search() {
      $http.get(`/unionOpportunity/expense?current=1&unionId=${this.unionId}&fromMemberId=${this.fromMemberId}&` + this.value + '=' + this.input)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records;
            this.totalAll = res.data.data.total;
            this.tableData.forEach((v, i) => {
              v.lastModifyTime = $todate.todate(new Date(v.lastModifyTime));
              switch (v.isClose) {
                case 0:
                  v.isClose = '';
                  break;
                case 1:
                  v.isClose = '';
                  break;
              };
              switch (v.opportunityType) {
                case 1:
                  v.opportunityType = '';
                  break;
                case 2:
                  v.opportunityType = '';
                  break;
              };
            });
          } else {
            this.tableData = [];
            this.totalAll = 0;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    handleCurrentChange(val) {
      $http.get(`/unionOpportunity/expense?current=${val}&unionId=${this.unionId}&fromMemberId=${this.fromMemberId}&` + this.value + '=' + this.input)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.records;
            this.totalAll = res.data.data.total;
            this.tableData.forEach((v, i) => {
              v.lastModifyTime = $todate.todate(new Date(v.lastModifyTime));
              switch (v.isClose) {
                case 0:
                  v.isClose = '';
                  break;
                case 1:
                  v.isClose = '';
                  break;
              };
              switch (v.opportunityType) {
                case 1:
                  v.opportunityType = '';
                  break;
                case 2:
                  v.opportunityType = '';
                  break;
              };
            });
          } else {
            this.tableData = [];
            this.totalAll = 0;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    pay(scope) {
      this.brokeragePrice = scope.row.brokeragePrice.toFixed(2);
      this.opportunityId = scope.row.opportunityId;
      this.dialogVisible = true;
      $http.get(`/unionOpportunity/qrCode/${this.opportunityId}`)
        .then(res => {
          if (res.data.data) {
            this.imgSrc = res.data.data.url;
            this.only = res.data.data.only;
          } else {
            this.imgSrc = '';
          };
        })
        .then(res => {
          setTimeout(() => {
            let timer2 = setInterval(() => {
              $http.get(`unionOpportunity/status/${this.only}`)
                .then(res => {
                  if (res.data.data == '003') {
                    this.$message({ showClose: true, message: '', type: 'success', duration: 5000 });
                    clearInterval(timer2);
                    this.visible3 = false;
                    this.visible4 = true;
                    let timer3 = setInterval(() => {
                      if (this.countTime == 0) {
                        clearInterval(timer3);
                        this.init();
                      }
                      this.countTime--;
                    }, 1000);
                  } else if (res.data.data == '004') {
                    this.$message({ showClose: true, message: '', type: 'warning', duration: 5000 });
                    clearInterval(timer2);
                  } else if (res.data.data == '005') {
                    this.$message({ showClose: true, message: '', type: 'warning', duration: 5000 });
                    clearInterval(timer2);
                  }
                })
            }, 1000);
          }, 3000);
        })
        .then(res => {
          this.search();
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    payAll() {
      let ids = '';
      this.brokeragePrice = 0;
      this.multipleSelection.forEach((v, i) => {
        this.brokeragePrice += Number(v.brokeragePrice);
        ids += v.opportunityId + '%2C';
      });
      this.dialogVisible = true;
      $http.get(`/unionOpportunity/qrCode/${ids}`)
        .then(res => {
          if (res.data.data) {
            this.imgSrc = res.data.data.url;
            this.only = res.data.data.only;
          } else {
            this.imgSrc = '';
          };
        })
        .then(res => {
          setTimeout(() => {
            let timer2 = setInterval(() => {
              $http.get(`unionOpportunity/status/${this.only}`)
                .then(res => {
                  if (res.data.data == '003') {
                    this.$message({ showClose: true, message: '', type: 'success', duration: 5000 });
                    clearInterval(timer2);
                    this.visible3 = false;
                    this.visible4 = true;
                    let timer3 = setInterval(() => {
                      if (this.countTime == 0) {
                        clearInterval(timer3);
                        this.init();
                      }
                      this.countTime--;
                    }, 1000);
                  } else if (res.data.data == '004') {
                    this.$message({ showClose: true, message: '', type: 'warning', duration: 5000 });
                    clearInterval(timer2);
                  } else if (res.data.data == '005') {
                    this.$message({ showClose: true, message: '', type: 'warning', duration: 5000 });
                    clearInterval(timer2);
                  }
                })
            }, 1000)
          }, 3000);
        })
        .then(res => {
          this.search();
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    filterTag(value, row) {
      return row.isClose === value;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
      if (this.multipleSelection.length) {
        this.canPay = true;
        this.multipleSelection.forEach((v, i) => {
          if (v.isClose === '') {
            this.canPay = false;
          }
        })
      } else {
        this.canPay = false;
      }
    },
    // 
    resetData() {
      // clearInterval(this.pay.timer2);
      // clearInterval(this.payAll().timer2);
    },
  }
}
</script>

<style lang='less' rel="stylesheet/less" scoped>
.imgSrc {
  width: 200px;
  height: 200px;
}
</style>




