<template>
  <div>
    <Breadcrumb :header-name="''"></Breadcrumb>
    <div class="container" id="Refund">
      <el-tabs v-model="activeName" v-if="isUnionOwner">
        <el-tab-pane label="" name="first">
          <div>
            <p style="text-align: right;color:#20A0FF">
              <span @click="dialogVisible1 = true" style="cursor: pointer">?</span>
            </p>
            <div style="margin-top: 25px">
              <el-table :data="tableData1">
                <el-table-column prop="outEnterpriseName" label="">
                </el-table-column>
                <el-table-column prop="applyOutTime" label="">
                </el-table-column>
                <el-table-column prop="applyOutReason" label="">
                </el-table-column>
                <el-table-column prop="" label="" width="150">
                  <template scope="scope">
                    <div class="sizeAndColor">
                      <el-button size="small" @click="agree(scope)"></el-button>
                      <el-button size="small" @click="disagree(scope)"></el-button>
                    </div>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
          <div class="footer">
            <el-pagination @current-change="handleCurrentChange1" :current-page.sync="currentPage1" :page-size="10" layout="prev, pager, next, jumper" :total="totalAll1">
            </el-pagination>
          </div>
        </el-tab-pane>
        <el-tab-pane label="" name="second">
          <p class="information"></p>
          <div>
            <div id="popBox">
              <el-button type="primary" @click="dialogVisible = true"  v-if="!isUnionOwner"></el-button>
              <!--   -->
              <el-dialog title="" :visible.sync="dialogVisible" size="tiny" @close="resetData">
                <hr>
                <div class="main">
                  <el-form ref="form" :model="form" label-width="80px">
                    <el-form-item label=":">
                      <el-input type="textarea" v-model="form.outReason" :rows="3" placeholder="">
                      </el-input>
                    </el-form-item>
                  </el-form>
                  <p style="width: 350px;overflow: hidden">
                    15
                  </p>
                </div>
                <span slot="footer" class="dialog-footer">
                  <el-button type="primary" @click="outConfirm"> </el-button>
                  <el-button @click="dialogVisible=false"> </el-button>
                </span>
              </el-dialog>
              <el-button type="warning"></el-button>
              <p class="fr" style="color:#20A0FF;cursor:pointer;" @click="dialogVisible1 = true">?</p>
            </div>
            <div style="margin-top: 25px" class="second">
              <el-table :data="tableData2">
                <el-table-column prop="outEnterpriseName" label="">
                </el-table-column>
                <el-table-column prop="confirmOutTime" label="">
                </el-table-column>
                <el-table-column prop="outType" label="">
                </el-table-column>
                <el-table-column prop="remainDay" label="" width="150">
                </el-table-column>
              </el-table>
              <div class="footer">
                <el-pagination @current-change="handleCurrentChange2" :current-page.sync="currentPage2" :page-size="10" layout="prev, pager, next, jumper" :total="totalAll2">
                </el-pagination>
              </div>
            </div>
          </div>
        </el-tab-pane>
      </el-tabs>
      <!--  -->
      <div class="tuimeng">
        <el-dialog title="" :visible.sync="dialogVisible1" size="tiny">
          <hr>
          <div class="model_12">
            <p>1. </p>
            <p>2. 
              <p>3. </p>
              <p>4. </p>
              <p>5. </p>
              <p>6. </p>
              <p>7. 15</p>
              <p>8. </p>
              <p>&nbsp;&nbsp;&nbsp;&nbsp;15</p>
              <p>9. </p>
              <p>10. </p>
            </p>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="dialogVisible1 = false"> </el-button>
          </span>
        </el-dialog>
      </div>
      <!--   -->
      <div class="tuimeng">
        <el-dialog title="" :visible.sync="dialogVisible2" size="tiny">
          <div class="model_12">
            <p>{{ outEnterpriseName }}?</p>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="confirm"></el-button>
            <el-button @click="dialogVisible2 = false"></el-button>
          </span>
        </el-dialog>
        <el-dialog title="" :visible.sync="dialogVisible3" size="tiny">
          <div class="model_12">
            <p>{{ outEnterpriseName }}</p>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="confirm1"></el-button>
            <el-button @click="dialogVisible3=false"></el-button>
          </span>
        </el-dialog>

      </div>
    </div>
  </div>
</template>

<script>
import Breadcrumb from '@/components/public-components/Breadcrumb'
import $http from '@/utils/http.js'
export default {
  name: 'union-quit',
  components: {
    Breadcrumb
  },
  data() {
    return {
      activeName: 'second',
      currentPage1: 1,
      currentPage2: 1,
      dialogVisible: false,
      dialogVisible1: false,
      dialogVisible2: false,
      dialogVisible3: false,
      tableData1: [],
      tableData2: [],
      form: {
        outReason: ''
      },
      outId: '',
      outEnterpriseName: '',
      totalAll1: 0,
      totalAll2: 0,
    }
  },
  computed: {
    isUnionOwner() {
      return this.$store.state.isUnionOwner;
    },
    unionMemberId() {
      return this.$store.state.unionMemberId;
    }
  },
  mounted: function() {
    $http.get(`/unionMemberOut/page/applyOut/memberId/${this.unionMemberId}?current=1`)
      .then(res => {
        if (this.isUnionOwner) {
          this.activeName = 'first';
        };
        if (res.data.data) {
          this.tableData1 = res.data.data.records;
          this.totalAll1 = res.data.data.total;
        };
      })
      .catch(err => {
        this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
      });
    $http.get(`/unionMemberOut/page/outing/memberId/${this.unionMemberId}?current=1`)
      .then(res => {
        if (res.data.data) {
          this.tableData2 = res.data.data.records;
          this.totalAll2 = res.data.data.total;
          this.tableData2.forEach((v, i) => {
            v.remainDay += '';
            switch (v.outType) {
              case 1:
                v.outType = '';
                break;
              case 2:
                v.outType = '';
                break;
            }
          });
        }

      })
      .catch(err => {
        this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
      })
  },
  methods: {
    //  
    agree(scope) {
      this.dialogVisible2 = true;
      this.outId = scope.row.outId;
      this.outEnterpriseName = scope.row.outEnterpriseName;
    },
    //  
    disagree(scope) {
      this.dialogVisible3 = true;
      this.outId = scope.row.outId;
      this.outEnterpriseName = scope.row.outEnterpriseName;
    },
    //   
    confirm() {
      $http.put(`/unionMemberOut/${this.outId}/memberId/${this.unionMemberId}?isOK=1`)
        .then(res => {
          this.dialogVisible2 = false;
          $http.get(`/unionMemberOut/page/applyOut/memberId/${this.unionMemberId}?current=1`)
            .then(res => {
              if (res.data.data) {
                this.tableData1 = res.data.data.records;
                this.totalAll1 = res.data.data.total;
              }
            })
            .catch(err => {
              this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
            });
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    //   
    confirm1() {
      $http.put(`/unionMemberOut/${this.outId}/memberId/${this.unionMemberId}?isOK=0`)
        .then(res => {
          this.dialogVisible3 = false;
          $http.get(`/unionMemberOut/page/applyOut/memberId/${this.unionMemberId}?current=1`)
            .then(res => {
              if (res.data.data) {
                this.tableData1 = res.data.data.records;
                this.totalAll1 = res.data.data.total;
              }
            })
            .catch(err => {
              this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
            });
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    handleCurrentChange1(val) {
      $http.get(`/unionMemberOut/page/applyOut/memberId/${this.unionMemberId}?current=${val}`)
        .then(res => {
          if (res.data.data) {
            this.tableData1 = res.data.data.records;
            this.totalAll1 = res.data.data.total;
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    handleCurrentChange2(val) {
      $http.get(`/unionMemberOut/page/outing/memberId/${this.unionMemberId}?current=${val}`)
        .then(res => {
          if (res.data.data) {
            this.tableData2 = res.data.data.records;
            this.totalAll2 = res.data.data.total;
            this.tableData2.forEach((v, i) => {
              v.remainDay += '';
              switch (v.outType) {
                case 1:
                  v.outType = '';
                  break;
                case 2:
                  v.outType = '';
                  break;
              }
            });
          }
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    outConfirm() {
      $http.post(`/unionMemberOut/memberId/${this.unionMemberId}/applyOut?applyOutReason=${this.form.outReason}`)
        .then(res => {
          $http.get(`/unionMemberOut/page/outing/memberId/${this.unionMemberId}?current=1`)
            .then(res => {
              this.tableData2 = res.data.data.records;
              this.totalAll2 = res.data.data.total;
              this.tableData2.forEach((v, i) => {
                v.remainDay += '';
                switch (v.outType) {
                  case 1:
                    v.outType = '';
                    break;
                  case 2:
                    v.outType = '';
                    break;
                }
              })
            })
        })
        .then(res => {
          this.dialogVisible = false
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    resetData() {
      this.form.outReason = '';
    },
  }
}
</script>
<style lang='less' rel="stylesheet/less" scoped>
.container {
  margin: 40px 50px;
  .second {
    margin-top: 11px!important;
  }
  .main {
    margin-bottom: 45px;
    p {
      margin-left: 140px;
      color: #999999;
    }
  }
  .tuimeng {
    .model_12 {
      margin-left: 30px;
      margin-top: 20px;
      p {
        margin-bottom: 7px;
        color: #333333;
      }
    }
  }
}
</style>



