<template>
  <div>
    <div>
      <el-form :inline="true" class="demo-form-inline">
        <el-form-item label="">
          <el-select v-model="value" placeholder="" @change="change">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
    </div>
    <div id="echart1">
      echart1
    </div>
    <div id="echart2">
      echart2
    </div>
    <div id="echart3">
      echart3
    </div>
  </div>
</template>

<script>
import $http from '@/utils/http.js'
import echarts from 'echarts'
export default {
  name: 'datachart',
  data() {
    return {
      value: '',
      options: [],
      statisticData: {},
    }
  },
  mounted: function() {
    // 
    $http.get(`/unionMember/listMap`)
      .then(res => {
        if (res.data.data) {
          this.options = res.data.data;
          res.data.data.forEach((v, i) => {
            this.options[i].value = v.unionMember.id;
            this.options[i].label = v.unionMain.name;
          });
          this.value = this.options[0].value;
        } else {
          this.options = [];
        }
      })
      .then(res => {
        // 
        $http.get(`/unionOpportunity/memberId/${this.value}/statistics`)
          .then(res => {
            if (res.data.data) {
              this.statisticData = res.data.data;
            } else {
              this.statisticData = {};
            }
            // this.statisticData.brokerageExpenseSum = 0;
            // this.statisticData.brokerageIncomeSum = 0;
            // this.statisticData.paidBrokerageExpense = 0;
            // this.statisticData.paidBrokerageIncome = 0;
            // this.statisticData.unPaidBrokerageExpense = 0;
            // this.statisticData.unPaidBrokerageIncome = 0;
          })
          .then(res => {
            // 
            let myChart1 = echarts.init(document.getElementById('echart1'));
            myChart1.setOption({
              title: {
                text: ` ${this.statisticData.brokerageIncomeSum}`,
                x: '230',
                top: 270,
                textStyle: {
                  color: '#ff9348',
                  fontWeight: 'bold'
                },
                subtext: ''
              },
              tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b}: {c} ({d}%)"
              },
              legend: {
                orient: 'vertical',
                x: 'left',
                data: ['', '']
              },
              color: ['#20A0FF', '#20A0FF'],
              series: [
                {
                  name: '',
                  type: 'pie',
                  radius: ['35%', '80%'],
                  center: ['70%', '45%'],
                  avoidLabelOverlap: false,
                  label: {
                    normal: {
                      show: false,
                      position: 'center'
                    },
                    emphasis: {
                      show: true,
                      textStyle: {
                        fontSize: '20',
                        fontWeight: 'bold'
                      }
                    }
                  },
                  labelLine: {
                    normal: {
                      show: false
                    }
                  },
                  data: [
                    {
                      value: this.statisticData.paidBrokerageIncome,
                      name: '',
                      itemStyle: {
                        normal: {
                          color: '#20A0FF'
                        }
                      }
                    },
                    {
                      value: this.statisticData.unPaidBrokerageIncome,
                      name: '',
                      itemStyle: {
                        normal: {
                          color: '#2EC38A'
                        }
                      }
                    },
                  ]
                }
              ]
            });
            let myChart2 = echarts.init(document.getElementById('echart2'));
            myChart2.setOption({
              title: {
                text: ` ${this.statisticData.brokerageExpenseSum}`,
                x: '230',
                top: 270,
                textStyle: {
                  color: '#ff9348',
                  fontWeight: 'bold'
                },
                subtext: ''
              },
              tooltip: {
                trigger: 'item',
                formatter: "{a} <br/>{b}: {c} ({d}%)"
              },
              legend: {
                orient: 'vertical',
                x: '50',
                data: ['', '']
              },
              color: ['#20A0FF', '#20A0FF'],
              series: [
                {
                  name: '',
                  type: 'pie',
                  radius: ['35%', '80%'],
                  center: ['70%', '45%'],
                  avoidLabelOverlap: false,
                  label: {
                    normal: {
                      show: false,
                      position: 'center'
                    },
                    emphasis: {
                      show: true,
                      textStyle: {
                        fontSize: '20',
                        fontWeight: 'bold'
                      }
                    }
                  },
                  labelLine: {
                    normal: {
                      show: false
                    }
                  },
                  data: [
                    {
                      value: this.statisticData.paidBrokerageExpense,
                      name: '',
                      itemStyle: {
                        normal: {
                          color: '#20A0FF'
                        }
                      }
                    },
                    {
                      value: this.statisticData.unPaidBrokerageExpense,
                      name: '',
                      itemStyle: {
                        normal: {
                          color: '#2EC38A'
                        }
                      }
                    },
                  ]
                }
              ]
            });
            let myChart3 = echarts.init(document.getElementById('echart3'));
            let incomeData = [];
            let payData = [];
            // 
            incomeData.push(this.statisticData.brokerageInWeek[0]['Monday'].paidBrokerageIncome);
            incomeData.push(this.statisticData.brokerageInWeek[1]['Tuesday'].paidBrokerageIncome);
            incomeData.push(this.statisticData.brokerageInWeek[2]['Wednesday'].paidBrokerageIncome);
            incomeData.push(this.statisticData.brokerageInWeek[3]['Thursday'].paidBrokerageIncome);
            incomeData.push(this.statisticData.brokerageInWeek[4]['Friday'].paidBrokerageIncome);
            incomeData.push(this.statisticData.brokerageInWeek[5]['Saturday'].paidBrokerageIncome);
            incomeData.push(this.statisticData.brokerageInWeek[6]['Sunday'].paidBrokerageIncome);
            // 
            payData.push(this.statisticData.brokerageInWeek[0]['Monday'].paidBrokerageExpense);
            payData.push(this.statisticData.brokerageInWeek[1]['Tuesday'].paidBrokerageExpense);
            payData.push(this.statisticData.brokerageInWeek[2]['Wednesday'].paidBrokerageExpense);
            payData.push(this.statisticData.brokerageInWeek[3]['Thursday'].paidBrokerageExpense);
            payData.push(this.statisticData.brokerageInWeek[4]['Friday'].paidBrokerageExpense);
            payData.push(this.statisticData.brokerageInWeek[5]['Saturday'].paidBrokerageExpense);
            payData.push(this.statisticData.brokerageInWeek[6]['Sunday'].paidBrokerageExpense);
            myChart3.setOption({
              title: {
                text: '',
                subtext: '',
                subtextStyle: {//
                  color: '#666666',
                  fontStyle: 'normal',//normalitalic(),oblique()
                  fontWeight: "lighter",//normal()bold()bolder()lighter()100|200|300|400|500...
                  fontSize: 12,//12px
                },
                itemGap: 20,//
                left: "left",
              },
              tooltip: {
                trigger: 'axis',
                axisPointer: {            // 
                  type: 'shadow'        // 'line' | 'shadow'
                }
              },
              legend: {
                data: ['', ''],
                left: 'right'
              },
              grid: {
                left: '0%',
                right: '0%',
                top: '30%',
                bottom: '0%',
                height: '252px',
                containLabel: true
              },
              xAxis: [
                {
                  type: 'category',
                  data: ['', '', '', '', '', '', '']
                }
              ],
              yAxis: [
                {
                  type: 'value'
                }
              ],
              series: [
                {
                  name: '',
                  type: 'bar',
                  data: incomeData,
                  itemStyle: {
                    normal: {
                      color: '#20A0FF'
                    }
                  },
                  barWidth: 20//
                },
                {
                  name: '',
                  type: 'bar',
                  data: payData,
                  itemStyle: {
                    normal: {
                      color: '#2EC38A'
                    }
                  },
                  barWidth: 20
                }
              ]
            });
          })
          .catch(err => {
            this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
          });
      })
      .catch(err => {
        this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
      })
    window.addEventListener('resize', this.onresize);
  },
  methods: {
    /*
    * 
    * */
    onresize() {
      document.querySelectorAll('#echart3')[0].style.width = window.innerWidth - 170 + 'px';
    },
    change() {
      // 
      $http.get(`/unionOpportunity/memberId/${this.value}/statistics`)
        .then(res => {
          this.statisticData = res.data.data;
          // this.statisticData.brokerageExpenseSum = 0;
          // this.statisticData.brokerageIncomeSum = 0;
          // this.statisticData.paidBrokerageExpense = 0;
          // this.statisticData.paidBrokerageIncome = 0;
          // this.statisticData.unPaidBrokerageExpense = 0;
          // this.statisticData.unPaidBrokerageIncome = 0;
        })
        .then(res => {
          // 
          let myChart1 = echarts.init(document.getElementById('echart1'));
          myChart1.setOption({
            title: {
              text: ` ${this.statisticData.brokerageIncomeSum}`,
              x: '230',
              top: 270,
              textStyle: {
                color: '#ff9348',
                fontWeight: 'bold'
              },
              subtext: ''
            },
            tooltip: {
              trigger: 'item',
              formatter: "{a} <br/>{b}: {c} ({d}%)"
            },
            legend: {
              orient: 'vertical',
              x: 'left',
              data: ['', '']
            },
            color: ['#20A0FF', '#20A0FF'],
            series: [
              {
                name: '',
                type: 'pie',
                radius: ['35%', '80%'],
                center: ['70%', '45%'],
                avoidLabelOverlap: false,
                label: {
                  normal: {
                    show: false,
                    position: 'center'
                  },
                  emphasis: {
                    show: true,
                    textStyle: {
                      fontSize: '20',
                      fontWeight: 'bold'
                    }
                  }
                },
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                data: [
                  {
                    value: this.statisticData.paidBrokerageIncome,
                    name: '',
                    itemStyle: {
                      normal: {
                        color: '#20A0FF'
                      }
                    }
                  },
                  {
                    value: this.statisticData.unPaidBrokerageIncome,
                    name: '',
                    itemStyle: {
                      normal: {
                        color: '#2EC38A'
                      }
                    }
                  },
                ]
              }
            ]
          });
          let myChart2 = echarts.init(document.getElementById('echart2'));
          myChart2.setOption({
            title: {
              text: ` ${this.statisticData.brokerageExpenseSum}`,
              x: '230',
              top: 270,
              textStyle: {
                color: '#ff9348',
                fontWeight: 'bold'
              },
              subtext: ''
            },
            tooltip: {
              trigger: 'item',
              formatter: "{a} <br/>{b}: {c} ({d}%)"
            },
            legend: {
              orient: 'vertical',
              x: '50',
              data: ['', '']
            },
            color: ['#20A0FF', '#20A0FF'],
            series: [
              {
                name: '',
                type: 'pie',
                radius: ['35%', '80%'],
                center: ['70%', '45%'],
                avoidLabelOverlap: false,
                label: {
                  normal: {
                    show: false,
                    position: 'center'
                  },
                  emphasis: {
                    show: true,
                    textStyle: {
                      fontSize: '20',
                      fontWeight: 'bold'
                    }
                  }
                },
                labelLine: {
                  normal: {
                    show: false
                  }
                },
                data: [
                  {
                    value: this.statisticData.paidBrokerageExpense,
                    name: '',
                    itemStyle: {
                      normal: {
                        color: '#20A0FF'
                      }
                    }
                  },
                  {
                    value: this.statisticData.unPaidBrokerageExpense,
                    name: '',
                    itemStyle: {
                      normal: {
                        color: '#2EC38A'
                      }
                    }
                  },
                ]
              }
            ]
          });
          let myChart3 = echarts.init(document.getElementById('echart3'));
          let incomeData = [];
          let payData = [];
          // 
          incomeData.push(this.statisticData.brokerageInWeek[0]['Monday'].paidBrokerageIncome);
          incomeData.push(this.statisticData.brokerageInWeek[1]['Tuesday'].paidBrokerageIncome);
          incomeData.push(this.statisticData.brokerageInWeek[2]['Wednesday'].paidBrokerageIncome);
          incomeData.push(this.statisticData.brokerageInWeek[3]['Thursday'].paidBrokerageIncome);
          incomeData.push(this.statisticData.brokerageInWeek[4]['Friday'].paidBrokerageIncome);
          incomeData.push(this.statisticData.brokerageInWeek[5]['Saturday'].paidBrokerageIncome);
          incomeData.push(this.statisticData.brokerageInWeek[6]['Sunday'].paidBrokerageIncome);
          // 
          payData.push(this.statisticData.brokerageInWeek[0]['Monday'].paidBrokerageExpense);
          payData.push(this.statisticData.brokerageInWeek[1]['Tuesday'].paidBrokerageExpense);
          payData.push(this.statisticData.brokerageInWeek[2]['Wednesday'].paidBrokerageExpense);
          payData.push(this.statisticData.brokerageInWeek[3]['Thursday'].paidBrokerageExpense);
          payData.push(this.statisticData.brokerageInWeek[4]['Friday'].paidBrokerageExpense);
          payData.push(this.statisticData.brokerageInWeek[5]['Saturday'].paidBrokerageExpense);
          payData.push(this.statisticData.brokerageInWeek[6]['Sunday'].paidBrokerageExpense);
          myChart3.setOption({
            title: {
              text: '',
              subtext: '',
              subtextStyle: {//
                color: '#666666',
                fontStyle: 'normal',//normalitalic(),oblique()
                fontWeight: "lighter",//normal()bold()bolder()lighter()100|200|300|400|500...
                fontSize: 12,//12px
              },
              itemGap: 20,//
              left: "left",
            },
            tooltip: {
              trigger: 'axis',
              axisPointer: {            // 
                type: 'shadow'        // 'line' | 'shadow'
              }
            },
            legend: {
              data: ['', ''],
              left: 'right'
            },
            grid: {
              left: '0%',
              right: '0%',
              top: '30%',
              bottom: '0%',
              height: '252px',
              containLabel: true
            },
            xAxis: [
              {
                type: 'category',
                data: ['', '', '', '', '', '', '']
              }
            ],
            yAxis: [
              {
                type: 'value'
              }
            ],
            series: [
              {
                name: '',
                type: 'bar',
                data: incomeData,
                itemStyle: {
                  normal: {
                    color: '#20A0FF'
                  }
                },
                barWidth: 20//
              },
              {
                name: '',
                type: 'bar',
                data: payData,
                itemStyle: {
                  normal: {
                    color: '#2EC38A'
                  }
                },
                barWidth: 20
              }
            ]
          });
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    }
  }
}
</script>

<style>
#echart1 {
  width: 500px;
  height: 300px;
  float: left;
  margin-right: 100px;
}

#echart2 {
  width: 500px;
  height: 300px;
  float: left;
}

#echart3 {
  width: 1080px;
  height: 400px;
  float: left;
  margin-top: 50px;
}
</style>





