<template>
  <!--  -->
  <div>
    <div class="preferenceItems clearfix">
      <p class="fl">
        <el-button type="primary" @click="dialogVisible = true"></el-button>
        <!--   -->
        <el-dialog :visible.sync="dialogVisible" size="tiny" @close="resetData">
          <el-row>
            <el-col :span="12" :offset="2">
              <el-input v-model="serviceName" placeholder=""></el-input>
            </el-col>
          </el-row>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="save"> </el-button>
            <el-button @click="dialogVisible=false"> </el-button>
          </span>
        </el-dialog>
        <el-button type="primary" @click="submitAll" v-if="!isUnionOwner"></el-button>
        <el-button type="warning">
          <i class="el-icon-caret-right el-icon--left"></i></el-button>
      </p>
      <div class="fr">
        <illustration></illustration>
      </div>
      <div class="footer">
        <el-table :data="tableData" style="width: 100%" ref="multipleTable" @selection-change="handleSelectionChange">
          <el-table-column type="selection" width="55">
          </el-table-column>
          <el-table-column prop="name" label="">
          </el-table-column>
          <el-table-column prop="status" label="" :filters="[{ text: '', value: '' }, { text: '', value: '' }, { text: '', value: '' }, { text: '', value: '' }]" :filter-method="filterTag" filter-placement="bottom-end">
          </el-table-column>
          <el-table-column label="" ref="scope">
            <template scope="scope">
              <div class="sizeAndColor">
                <el-button size="small" @click="submit(scope)" v-if="scope.row.status === '' || scope.row.status === ''">
                  
                </el-button>
                <el-button size="small" @click="del(scope)"></el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div>
          <div class="sizeAndColor fl">
            <el-button @click="selectAll()"></el-button>
            <el-button @click="toggleSelection()"></el-button>
            <el-button @click="deleteAll()"></el-button>
          </div>
          <el-pagination @current-change="handleCurrentChange" :current-page.sync="currentPage" :page-size="10" layout="prev, pager, next, jumper" :total="totalAll">
          </el-pagination>
        </div>
      </div>
      <!--   -->
      <div class="model_1">
        <el-dialog title="" :visible.sync="visible1" size="tiny">
          <hr>
          <div>
            <img src="../../assets/images/delect01.png"  class="fl">
            <span> {{ name }} </span>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="confirm1"></el-button>
            <el-button @click="visible1=false"></el-button>
          </span>
        </el-dialog>
    </div>
      <!--   -->
      <div class="model_1">
        <el-dialog title="" :visible.sync="visible2" size="tiny">
          <hr>
          <div>
            <img src="../../assets/images/delect01.png"  class="fl">
            <span></span>
          </div>
          <span slot="footer" class="dialog-footer">
            <el-button type="primary" @click="confirm2"></el-button>
            <el-button @click="visible2=false"></el-button>
          </span>
        </el-dialog>
      </div>
    </div>
  </div>
</template>

<script>
import $http from '@/utils/http.js'
import Illustration from './MyDiscountProjectIllustration'
export default {
  name: 'my-discount-project',
  components: {
    Illustration,
  },
  data() {
    return {
      tableData: [],
      currentPage: 1,
      dialogVisible: false,
      serviceName: '',
      totalAll: 0,
      visible1: false,
      visible2: false,
      id: '',
      name: '',
    }
  },
  computed: {
    unionMemberId() {
      return this.$store.state.unionMemberId;
    },
    isUnionOwner() {
      return this.$store.state.isUnionOwner;
    },
  },
  mounted: function() {
    this.init();
  },
  methods: {
    init() {
      $http.get(`/unionPreferentialProject/myProject/memberId/${this.unionMemberId}?current=1`)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.pageItem.records;
            this.totalAll = res.data.data.total;
            this.tableData.forEach((v, i) => {
              switch (v.status) {
                case 1:
                  v.status = '';
                  break;
                case 2:
                  v.status = '';
                  break;
                case 3:
                  v.status = '';
                  break;
                case 4:
                  v.status = '';
                  break;
              }
            });
          } else {
            this.tableData = [];
            this.totalAll = 0;
          };
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    save() {
      let url = `unionPreferentialItem/memberId/${this.unionMemberId}`
      let data = this.serviceName;
      $http.post(url, data)
        .then(res => {
          this.init();
        })
        .then(res => {
          this.dialogVisible = false;
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    submit(scope) {
      let url = `/unionPreferentialItem/batch/status/2/memberId/${this.unionMemberId}`;
      let data = [];
      data.push(scope.row.id);
      $http.put(url, data)
        .then(res => {
          this.init();
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    submitAll() {
      let url = `/unionPreferentialItem/batch/status/2/memberId/${this.unionMemberId}`;
      let data = [];
      this.multipleSelection.forEach((v, i) => {
        data.push(v.id);
      });
      $http.put(url, data)
        .then(res => {
          this.init();
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    del(scope) {
      this.name = scope.row.name;
      this.id = scope.row.id;
      this.visible1 = true;

    },
    // 
    confirm1() {
      this.visible1 = false;
      let url = `unionPreferentialItem/batch/delStatus/1/memberId/${this.unionMemberId}`;
      let data = [];
      data.push(this.id);
      $http.put(url, data)
        .then(res => {
          this.init();
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    deleteAll() {
      this.visible2 = true;
    },
    // 
    confirm2() {
      this.visible2 = false;
      let url = `unionPreferentialItem/batch/delStatus/1/memberId/${this.unionMemberId}`;
      let data = [];
      this.multipleSelection.forEach((v, i) => {
        data.push(v.id);
      });
      $http.put(url, data)
        .then(res => {
          this.init();
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        })
    },
    // 
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    // 
    selectAll() {
      this.tableData.forEach(item => {
        this.$refs.multipleTable.toggleRowSelection(item, true);
      });
    },
    // 
    handleCurrentChange(val) {
      $http.get(`/unionPreferentialProject/myProject/memberId/${this.unionMemberId}?current=${val}`)
        .then(res => {
          if (res.data.data) {
            this.tableData = res.data.data.pageItem.records;
            this.totalAll = res.data.data.total;
            this.tableData.forEach((v, i) => {
              switch (v.status) {
                case 1:
                  v.status = '';
                  break;
                case 2:
                  v.status = '';
                  break;
                case 3:
                  v.status = '';
                  break;
                case 4:
                  v.status = '';
                  break;
              }
            });
          } else {
            this.tableData = [];
            this.totalAll = 0;
          };
        })
        .catch(err => {
          this.$message({ showClose: true, message: err.toString(), type: 'error', duration: 5000 });
        });
    },
    // 
    filterTag(value, row) {
      return row.status === value;
    },
    // 
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    // 
    resetData() {
      this.serviceName = '';
    },
  }
}
</script>


<style lang='less' rel="stylesheet/less" scoped>
.preferenceItems {
  p {
    margin-top: 90px;
  }
  h4 {
    margin-bottom: 14px;
  }
}
</style>



